/// <reference path="../../typings/node/node.d.ts" />
/// <reference path="../../typings/chalk/chalk.d.ts" />
/// <reference path="../../typings/lodash/lodash.d.ts" />
"use strict";
const chalk_1 = require("chalk");
let unionBy = require('lodash.unionby');
const indent = '  ';
class Util {
    static copyObject(object) {
        return JSON.parse(JSON.stringify(object));
    }
    static unionBy(ts1, ts2, filter) {
        return unionBy(ts1, ts2, filter);
    }
    static reportErrorAndExit(error) {
        Log.red(`${indent}[${error.name}] `, error.message);
        process.exit(-1);
    }
    static reportMessageAndExit(message) {
        Log.red(`${indent}[ERROR] `, message);
        process.exit(-1);
    }
    static exitProcess() {
        process.exit(1);
    }
}
exports.Util = Util;
class Log {
    static blue(tag, message) {
        console.log(`${chalk_1.blue(tag)}${message}`);
    }
    static red(tag, message) {
        console.log(`${chalk_1.red(tag)}${message}`);
    }
    static green(tag, message) {
        console.log(`${chalk_1.green(tag)}${message}`);
    }
    static yellow(tag, message) {
        console.log(`${chalk_1.yellow(tag)}${message}`);
    }
    static magenta(tag, message) {
        console.log(`${chalk_1.magenta(tag)}${message}`);
    }
    static blueReverse(tag, message) {
        console.log(`${tag}${chalk_1.blue(message)}`);
    }
    static redReverse(tag, message) {
        console.log(`${tag}${chalk_1.red(message)}`);
    }
    static greenReverse(tag, message) {
        console.log(`${tag}${chalk_1.green(message)}`);
    }
    static yellowReverse(tag, message) {
        console.log(`${tag}${chalk_1.yellow(message)}`);
    }
    static magentaReverse(tag, message) {
        console.log(`${tag}${chalk_1.magenta(message)}`);
    }
    static info(message) {
        Log.green(`${indent}[INFO] `, message);
    }
    static warn(message) {
        Log.red(`${indent}[WARN] `, message);
    }
}
exports.Log = Log;
